.profile__information {
    margin: 0 0 0 30px;
    padding: 0;
    display: grid;
    grid-template-columns: minmax(min-content, max-content) minmax(min-content, max-content);
    align-items: center;
    box-sizing: border-box;
    gap: 16px 18px;
}

@media screen and (max-width: 767px) {
    .profile__info {
        margin-top: 23px;
        grid-row-gap: 14px;
    }
}